<!-- 商品卡片（列表） -->
<template>
	<card-h show-tag>
		<!-- 标签 -->
		<template slot="tag">{{ tag }}</template>

		<template slot="img">
			<image src="" class="goods-item-img"></image>
		</template>

		<template slot="main-right-header">
			<view class="header-content">
				<text class="title">西瓜</text>
			</view>
		</template>

		<template slot="main-right-footer">
			<view class="footer-content">
				<price :price="9.99" :origin-pirce="99.99" :vip-price="0.99"></price>

				<button type="button" class="add-to-cart-button">
					加入购物车
				</button>
			</view>

		</template>
	</card-h>
</template>

<script>
	export default {
		name: "goods-item",
		props: {
			data: Object
		},
		data() {
			return {
				tag: 'New'
			};
		}
	}
</script>

<style lang="scss">
	.header-content {
		width: 100%;
		padding-left: 10rpx;
	}
	
	.title {
		font-size: $uni-font-size-lg;
		font-weight: bolder;
		color: $uni-text-color-title;
	}
	
	.footer-content {
		width: 100%;
		padding-left: 10rpx;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
	}
	
	.add-to-cart-button {
		width: 200rpx;
		height: 40rpx;
		margin-left: 10rpx;
		margin-right: 10rpx;
		font-size: 5px;
		white-space: nowrap;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $uni-color-main;
		color: white;
	}
</style>
